import React, { Component } from 'react'; //imrc + tab
import { Link } from 'react-router-dom';
// import { bindActionCreators } from 'redux';
// import LogoutAction from '../redux/actions/LogoutAction';
import userlogo from "../../assets/img/profile/perfil.png";
// import logout_icon from '../images/logout_icon.svg';
// import Dropdown_user from './dropdown_user';
//import { Button } from "../Button/Button";
//fas fa-bars is for the hamburger menu
//fas fa-times is for the cancel
import './NewNav.css';
import './navbar.css';
class Navbar extends Component {

    state = { clicked: false }
    
    
    handleClick = () => {
        this.setState({ clicked: !this.state.clicked })
    }

    handleChange = () =>{
         this.props.actions();
    }

    render(){
        
        return(
            <nav className="NavbarItems">
           
                {/* <h1 className="navbar-logo"><i className="fab fa-react mb-5 mb-lg-0">KyuQuiz!</i></h1> */}
                
            <a href="#" className="header__logo">Jhon Doe</a>
  
                 <div className="nav__perfil d-none d-lg-flex">
                        <div className="nav__img">
                            <img src={userlogo} alt=""/>
                        </div>
                        
                        <div>
                            <a href="#" className="nav__name">Sourabh Kumar</a>
                            <span className="nav__profesion">Web designer</span>
                        </div>
                    </div>
                
                
                <div className="nav-menu-icon" onClick={this.handleClick}><i className={this.state.clicked ? 'fas fa-times' : 'fas fa-bars'}></i> </div>
                <div className="nav__menu">
                <ul className={this.state.clicked ? 'nav_list nav-menu active' : 'nav_list nav-menu'}>
                {/* <div className="nav__menu"> */}
                        {/* <ul className="nav__list"> */}
                            <li className="nav__item"><a href="#" className="nav__link active">Home</a></li>
                            <li className="nav__item"><a href="#" className="nav__link">About</a></li>
                            <li className="nav__item"><a href="#" className="nav__link">Skills</a></li>
                            <li className="nav__item"><a href="#" className="nav__link">Portfolio</a></li>
                            <li className="nav__item"><a href="#" className="nav__link">Contact</a></li>
                        {/* </ul> */}
                    {/* </div> */}
    
                    {/* <div class="nav__social">
                        <a href="#" className="nav__social-icon"><ion-icon name="logo-linkedin"></ion-icon></a>
                        <a href="#" className="nav__social-icon"><ion-icon name="logo-github"></ion-icon></a>
                        <a href="#" className="nav__social-icon"><ion-icon name="logo-behance"></ion-icon></a>
                    </div> */}
                </ul>
                </div>
            </nav>
        )
    }
}


export default Navbar

